html,body{
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	background-color: #222;
}
*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}


.content{
	width: 100%;
	height: 100%;
}

.stage{
	position: relative;
	width: 100%;
	height: 960px;
}
.img-sec{
	position: relative;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: #ddd;
	perspective:1800;
	-webkit-perspective:1800;
}


.img-figure {
	width: 400px;
	height: 600px;
	margin: 0;
	padding: 40px;
	position: absolute;
	box-sizing: border-box;
	border: 1px solid #999;
	background-color: #fff;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
	cursor: pointer;
	-webkit-transform-origin: 0 50% 0;
	        transform-origin: 0 50% 0;
	-webkit-transition: left .5s ease-in-out,top .5s ease-in-out,-webkit-transform .5s ease-in-out;
	transition: left .5s ease-in-out,top .5s ease-in-out,-webkit-transform .5s ease-in-out;
	transition: transform .5s ease-in-out,left .5s ease-in-out,top .5s ease-in-out;
	transition: transform .5s ease-in-out,left .5s ease-in-out,top .5s ease-in-out,-webkit-transform .5s ease-in-out;
}
/*.img-figure.is-inverse{
	transform: rotateY(180deg);
}*/

.img-figure figcaption{
	text-align: center;
}
.img-figure .img{
	/*transition: .1s;*/
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
}
.img-figure .img-title{
	margin-top: 20px;
	color: #333;
	font-size: 16px;
}
.img-figure .img-back{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	padding: 40px;
	background-color: #000;
	color: #fff;
	overflow: hidden;
	-webkit-backface-visibility: hidden;
	        backface-visibility: hidden;
	-webkit-transform: rotateY(-180deg);
	        transform: rotateY(-180deg);
	/*transition: .1s;*/
}
.img-figure.is-inverse{
	-webkit-transform: translate(400px) rotateY(-180deg);
	        transform: translate(400px) rotateY(-180deg);
}
.is-inverse .img-back{
	/*transform: rotateY(0deg);*/
}
.is-inverse .img{
	/*transform: rotateY(180deg);*/
}

.controller-nav{
	position: absolute;
	left: 0;
	bottom: 30px;
	width: 100%;
	text-align: center;
	z-index: 101;
}
.controller-utit {
	display: inline-block;
	width: 30px;
	height: 30px;
	margin: 0 6px;
	text-align: center;
	cursor: pointer;
	background-color: #aaa;
	border-radius: 50%;
	-webkit-transform: scale(.5);
	        transform: scale(.5);
	-webkit-transition: all 0.5s;
	transition: all 0.5s;
	vertical-align: middle;
}
.controller-utit.is-center{
	-webkit-transform: scale(1);
	        transform: scale(1);
	background-color: #888;
	/*font-smoothing:antialiased;*/

}
.controller-utit.is-inverse {
	background-color: #555;
	-webkit-transform: rotateY(180deg);
	        transform: rotateY(180deg);
}

